.card {
  border: 1px solid #f3f3f3;
  padding: 30px;
  width: 220px;
  border-radius: 40px;
  margin-right: 30px;
  margin-bottom: 30px;
  transition: box-shadow 0.2s ease-in-out, transform 0.2s ease-in-out;
}
.card h5 {
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
}
.card .favorite {
  position: absolute;
  cursor: pointer;
}

.card .plus {
  cursor: pointer;
}

.card span {
  font-size: 13px;
  text-transform: uppercase;
  color: #bdbdbd;
}
.card b {
  font-size: 14px;
}

.card:hover {
  box-shadow: 0px 14px 30px rgba(0, 0, 0, 0.05);
  transform: translateY(-5px);
}
