.overlay {
  position: absolute;
  left: 0;
  top: 0;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 9;
  width: 100%;
  height: 100%;
  visibility: hidden;
  opacity: 0;
  transition: opacity .1s ease-out, visibility .1s ease-out;
  overflow: hidden;
}
.overlayVisible {
    visibility: visible;
    opacity: 1;
}

.overlayVisible .drawer {
    transform: translateX(0);
}

.drawer {
  width: 420px;
  background-color: #fff;
  box-shadow: -10px 4px 24px rgba(0, 0, 0, 0.1);
  position: absolute;
  right: 0;
  height: 100%;
  display: flex;
  flex-direction: column;
  padding: 30px;
  transform: translateX(100%);
  transition: transform .3s ease-out;
  overflow: auto;
}

.drawer .items {
  overflow: auto;
  margin-bottom: 40px;
  flex: 1;
}

.drawer h2 {
  margin: 0;
  font-size: 24px;
}